import React from 'react'
import {
    Modal,
    Form,
    Input
} from 'antd'

type ModalType = {
    visible: boolean
    onCancel: () => void
    onOk: (filter: Record<string,any>) => void
}

const FilterModal: React.FC<ModalType> = ({
    visible,
    onCancel,
    onOk,
}) => {
    const [form] = Form.useForm()
    const [loading, toggleLoading] = React.useState(false)
    
    const changeFilter = async () => {
        toggleLoading(true)
        await onOk(form.getFieldsValue())
        toggleLoading(false)
        onCancel()
    }
    return (
        <Modal
            visible={visible}
            onCancel={onCancel}
            onOk={changeFilter}
            title="Lc h s"
            okButtonProps={{
                loading
            }}
        >
            <Form form={form} labelCol={{span: 6}} wrapperCol={{span:18}}>
                <Form.Item label="M h s" name="randomId">
                    <Input />
                </Form.Item>
                <Form.Item label="Tn hc vin" name="ten">
                    <Input />
                </Form.Item>
                <Form.Item label="S in thoi" name="mobile">
                    <Input />
                </Form.Item>
            </Form>
        </Modal>
    )
}
export default FilterModal