import React from 'react'
import {
    Button,
    message,
    Modal,
    Space,
    Table
} from 'antd'
import { DeleteOutlined, LockOutlined } from '@ant-design/icons'
import { Link } from 'react-router-dom'

type Ctype = {
    users: Record<string,any>[]
    deleteUser: (params: any) => void
}
const roles:Record<string,string> = {
    'TEACHER': 'Gio Vin',
    'ADMIN': 'NV P.o To',
    'FINANCE': 'K ton',
    'MANAGER': 'Qun l',
    'SUPER_ADMIN': 'Qun tr vin',
}
const UsersList:React.FC<Ctype> = ({
    users,
    deleteUser
}) => {
    const [confirmModal, toggleConfirmModal] = React.useState(false)
    const [deleting, toggleDeleting] = React.useState(false)
    const confirmDelete = async (name: string,_id: string) => {
        Modal.confirm({
            title:'Xa ti khon ngi dng',
            content: `Bn c mun kha ti khon $name`,
            visible: confirmModal,
            onCancel: () => toggleConfirmModal(false),
            onOk: async () => {
                try {
                    toggleDeleting(true)
                    await deleteUser({
                        variables: {
                            _id
                        }
                    })
                    message.success("Ti khon  c xa")
                    toggleDeleting(false)
                } catch (error) {
                    message.error('Khng th xa c ti khon')
                }
            },
            okButtonProps: {
                danger: true,
                loading: deleting
            }
        })
    }
    const columns = [
        {
            title: "Tn",
            dataIndex: 'name',
            render: (text: string, record: any) => {
                return (
                    <Space>
                        {record.disabled && <LockOutlined style={{ color: 'red'}} />}
                        <Link to={`/users/${record._id}`}>
                            {text}
                        </Link>
                    </Space>
                )
            }
        },
        {
            title: 'Email/phone',
            dataIndex: 'email'
        },
        {
            title: 'Vai tr / quyn',
            dataIndex: 'role',
            render: (text: string) => <span>{roles[text]}</span>
        },
        {
            title: 'Xa',
            render: (_text: string, record: any) => <Button onClick={() => confirmDelete(record.name, record._id)} icon={<DeleteOutlined />} danger type="text" />
        }
    ]
    return (
        <Table
            dataSource={users}
            columns ={columns}
        />
    )
}

export default UsersList