import React from 'react'
import {
    Form,
    Input,
    Button,
    message
} from 'antd'
import {
    gql,
    useMutation
} from '@apollo/client'

const CHANGE_PWD = gql `
    mutation changePwd($currentPwd: String!, $newPwd: String!, $confirmNewPwd: String!) {
        changePwd(currentPwd: $currentPwd, newPwd: $newPwd, confirmNewPwd: $confirmNewPwd)
    }
`

const PasswordForm = () => {
    const [form] = Form.useForm()
    const [changePwd, { loading }] = useMutation(CHANGE_PWD)
    const onChange = async () => {
        const { currentPwd, newPwd, confirmNewPwd} = form.getFieldsValue()
        if (!currentPwd || !newPwd || !confirmNewPwd) {
            message.error('Vui lng in y  thng tin')
            return
        }
        try {
            await changePwd({
                variables: {
                    currentPwd,
                    newPwd,
                    confirmNewPwd
                }
            })
            message.success('i mt khu thnh cng')
            form.resetFields()
        } catch (error) {
            message.error(' c li xy ra, vui lng kim tra thng tin')
            // return
        }
    }
    return (
        <Form form={form} labelCol={{span: 8}} wrapperCol={{ span: 16}}>
            <Form.Item label="Mt khu c" rules={[{required: true, message: 'Bt buc phi in'}]} name="currentPwd">
                <Input.Password />
            </Form.Item>
            <Form.Item label="Mt khu mi" rules={[{required: true, message: 'Bt buc phi in'}]} name="newPwd">
                <Input.Password />
            </Form.Item>
            <Form.Item label="Nhp li mt khu mi" rules={[{required: true, message: 'Bt buc phi in'}]} name="confirmNewPwd">
                <Input.Password />
            </Form.Item>
            <Button
                onClick={() => onChange()}
                type="primary"
                loading={loading}
            >
                i mt khu
            </Button>
        </Form>
    )
}
export default PasswordForm