import React from 'react'
import {
    Button,
    Descriptions,
    message,
    PageHeader
} from 'antd'
import {
    useParams
} from 'react-router-dom'
import {
    // gql,
    useLazyQuery,
    useMutation
} from '@apollo/client'
import LoadinCenter from '../../common/LoadingCenter'
import StudentsList from './StudentsList'
import {
    useAuth
} from '../../../context/AuthenticationContext'
import { CheckOutlined } from '@ant-design/icons'
import { trangthaibaocao } from '../../common/trangthai'
import moment from 'moment'

type ReportOneType = {
    loadReport: any,
    approveReport: any,
    markComplete: any,
    addStd: any,
    removeStd: any
    dataKey: string
    type: string
}

const SingleAttReport:React.FC<ReportOneType> = ({
    loadReport,
    approveReport,
    markComplete,
    addStd,
    removeStd,
    dataKey,
    type
}) => {
    const { getUser } = useAuth()
    const user = getUser()
    const params:any = useParams()
    const [loadR, { loading, data }] = useLazyQuery(loadReport)
    const [approveR, { loading: approving }] = useMutation(approveReport, { refetchQueries: [loadReport]})
    const [markCompleted] = useMutation(markComplete, { refetchQueries: [loadReport]})
    const [addToR1] = useMutation(addStd, {
        refetchQueries: [loadReport]
    })
    const [removeStds] = useMutation(removeStd, {
        refetchQueries: [loadReport]
    })
    const [filter, setFilter] = React.useState<Record<string,any>>({
        randomId: null,
        ten: null,
        mobile: null
    })

    React.useEffect(() => {
        const load = async () => {
            await loadR({
                variables: {
                    report: params.id,
                    filter
                }
            })
        }
        load()
    }, [loadR, params, filter])
    // const { id } = params

    const removeStudents = async (students: string[]) => {
        try {
            await removeStds({
                variables: {
                    students,
                    report: data[dataKey as keyof Object].report._id
                },
                update: () => {}
            })
            message.success('Loi h s thnh cng')
        } catch (e) {
            message.error('Khng th loi h s khi bo co - vui lng kim tra li')
        }
    } 

    const addStudents = async (students: string[]) => {
        try {
            const { report } = data[dataKey as keyof Object]
            if (!report || !report._id) {
                throw new Error('Bo co khng hp l')
            }
            addToR1({
                variables: {
                    students,
                    report: report._id
                }
            })
            message.success('Thm vo bo co thnh cng')
        } catch (e) {
            message.error('Khng th thm vo bo co')
        }
    }

    const markComp = async (students: string[]) => {
        try {
            await markCompleted({
                variables: {
                    students
                }
            })
            message.success(' nh du hon thnh')
        } catch (e) {
            message.error('khng th thc hin c thao tc')
        }
    }

    const approve = async () => {
        if (!data || !data[dataKey as keyof Object]) {
            message.error('Khng th duyt c bo co')
        } else {
            try {
                await approveR({
                    variables: {
                        report: data[dataKey as keyof Object].report._id
                    }
                })
                message.success('Ph duyt thnh cng')
            } catch (e) {
                message.error('Khng th duyt c bo co')
            }
        }
    }

    if (loading) {
        return <LoadinCenter />
    }
    const extra = []
    if (user.role === 'MANAGER' && type !=='BC2') {
        extra.push(
            (
                <Button disabled={!data || data[dataKey as keyof Object].report.status === 'APPROVED'} onClick={() => approve()} loading={approving} type="primary" shape="round">
                    <CheckOutlined />
                    Ph duyt
                </Button>
            )
        )
    }
    if (data && data[dataKey as keyof Object]) {
        const { report } = data[dataKey as keyof Object]
        return (
            <div>
                <PageHeader
                    title={`Bo co: ${report.name}`}
                    extra={extra}
                    subTitle={`${report.desc}`}
                >
                    <Descriptions size="small" column={3}>
                        <Descriptions.Item label="Hng Bng">{report.hangbang}</Descriptions.Item>
                        <Descriptions.Item label="Ngy to">{moment(parseFloat(report.created)).format('DD/MM/YYYY')}</Descriptions.Item>
                        <Descriptions.Item label="Trng thi">{trangthaibaocao[report.status as keyof Object]}</Descriptions.Item>
                        <Descriptions.Item label="Ngi to">{report.createdBy.name}</Descriptions.Item>
                    </Descriptions>
                </PageHeader>
                {/* <BasicInfor report={data[dataKey as keyof Object].report} /> */}
                <StudentsList
                    type={type}
                    report={data[dataKey as keyof Object].report}
                    filter={filter}
                    setFilter={setFilter}
                    students={data[dataKey as keyof Object].students}
                    removeStudents={removeStudents}
                    addStudents={addStudents}
                    markComp={markComp}
                />
            </div>
        )
    }
    return null
}
export default SingleAttReport