import React from 'react'
import {
    Descriptions
} from 'antd'
import moment from 'moment'

const roles: Record<string, string> = {
    'TEACHER': 'Gio Vin',
    'ADMIN': 'NV P.o To',
    'FINANCE': 'K ton',
    'MANAGER': 'Qun l',
    'SUPER_ADMIN': 'Qun tr vin',
}

type CType = {
    user: any
}
const BasicInfor: React.FC<CType> = ({
    user
}) => {
    return (
        <Descriptions title="Thng tin ngi dng">
            <Descriptions.Item label="Tn">
                <strong>{user.name}</strong>
            </Descriptions.Item>
            <Descriptions.Item label="Tn ng nhp/ email/ SDT">
                <strong>{user.email}</strong>
            </Descriptions.Item>
            <Descriptions.Item label="Vai tr">
                <strong>{roles[user.role]}</strong>
            </Descriptions.Item>
            <Descriptions.Item label="Trng thi">
                <strong>{user.disabled ? ' kha' : 'ang hot ng'}</strong>
            </Descriptions.Item>
            <Descriptions.Item label="Ngy to">
                <strong>{moment(parseFloat(user.created)).format('DD/MM/YYYY')}</strong>
            </Descriptions.Item>
        </Descriptions>
    )
}
export default BasicInfor 