import React from 'react'
import {
    Table
} from 'antd'
import moment from 'moment'
import {
    Link
} from 'react-router-dom'
import { trangthaibaocao } from '../../../common/trangthai'

type CType = {
    reports: Record<string,any>[]
    type: string
}

const ReportList:React.FC<CType> = ({
    reports,
    type
}) => {
    const columns = [
        {
            title: 'Ngy to',
            dataIndex: 'created',
            render: (text: string) => <span>{moment(parseFloat(text)).format('DD/MM/YYYY')}</span>
        },
        {
            title: 'Hng bng',
            dataIndex: 'hangbang'
        },
        {
            title: 'Tn bo co',
            dataIndex: 'name',
            render: (text: string, record: any) => <Link to={`${ type==='BC1'? '/rp1/':'/rp2/'}${record._id}`}>{text}</Link>
        },
        {
            title: 'Ch thch',
            dataIndex: 'desc'
        },
        {
            title: 'SL hc vin',
            dataIndex: 'stdCount',
            render: (text: string) => <strong>{text? text : '0'}</strong>
        },
        {
            title: 'Trng thi',
            dataIndex: 'status',
            render: (text: string) => (
                <span>
                    {trangthaibaocao[text as keyof Object]}
                </span>
            )
        }
    ]
    return (
        <div>
            <Table
                dataSource={reports}
                columns={columns}
                pagination={false}
            />
        </div>
    )
}

export default ReportList
