import React from 'react'
import {
    Modal,
    Form,
    Input,
    Select
} from 'antd'

type MType = {
    visible: boolean
    onCancel: () => void
    onOk: () => void
    createU: (user: Record<string,any>) => void
}

export const roles = {
    'TEACHER': 'Gio vin',
    'ADMIN': 'o to',
    'FINANCE': 'K ton',
    'MANAGER': 'Qun l - Gim c',
    'SUPER_ADMIN': 'Qun tr h thng'
}
const { Option } = Select
const CreateUserModal:React.FC<MType> = ({
    visible,
    onCancel,
    onOk,
    createU
}) => {
    const [form] = Form.useForm()
    const [loading, toggleLoading] = React.useState(false)
    const createUser = async () => {
        toggleLoading(true)
        const data = form.getFieldsValue()
        await createU(data)
        toggleLoading(false)
    }
    return (
        <Modal
            visible={visible}
            onCancel={onCancel}
            onOk={createUser}
            title="To ngi dng mi"
            okButtonProps={{ loading }}
        >
            <Form form={form} labelCol={{span: 8}} wrapperCol={{span: 16}}>
                <Form.Item label="Tn ngi dng" name="name" rules={[{required: true, message: 'Thng tin bt buc'}]}>
                    <Input />
                </Form.Item>
                <Form.Item label="Email/SDT" name="email" rules={[{ required: true, message: 'Thng tin bt buc' }]}>
                    <Input />
                </Form.Item>
                <Form.Item label="Mt khu" name="password" rules={[{ required: true, message: 'Thng tin bt buc' }]}>
                    <Input.Password />
                </Form.Item>
                <Form.Item label="Nhp li mt khu" name="confirmPassword" rules={[{ required: true, message: 'Thng tin bt buc' }]}>
                    <Input.Password />
                </Form.Item>
                <Form.Item label="Vai tr, chc nng" name="role" rules={[{ required: true, message: 'Thng tin bt buc' }]}>
                    <Select>
                        {Object.keys(roles).map(k => (
                            <Option key={k} value={k}>{roles[k as keyof Object]}</Option>
                        ))}
                    </Select>
                </Form.Item>
            </Form>
        </Modal>
    )
}
export default CreateUserModal