import React from 'react'
import {
    Modal,
    Form,
    Input,
    Select
} from 'antd'
import hangbang from '../../../common/HangBang'

const { Option } = Select
const trangthais = {
    'PENDING': 'ang ch ph duyt',
    'APPROVED': ' ph duyt'
}

type ReportFilterModalType = {
    visible: boolean,
    onCancel: () => void
    onOk: (filter: Record<string,any>) => void
    filter: Record<string, any>
}

const ReportFileterModal:React.FC<ReportFilterModalType> = ({
    visible,
    onCancel,
    onOk,
    filter
}) => {
    const [form] = Form.useForm()
    form.setFieldsValue(filter)
    const onConfirm = () => {
        onOk(form.getFieldsValue())
        onCancel()
    }
    return (
        <Modal
            title="Lc bo co"
            visible={visible}
            onCancel={onCancel}
            onOk={onConfirm}
        >
            <Form form={form} labelCol={{ span: 6}} wrapperCol={{ span: 18}}>
                <Form.Item label="Tn bo co" name="name">
                    <Input />
                </Form.Item>
                <Form.Item label="Ch thch" name="desc">
                    <Input />
                </Form.Item>
                <Form.Item label="Hng bng" name="hangbang">
                    <Select>
                        {Object.keys(hangbang).map(hb => (
                            <Option value={hb} key={hb}>{`${hb} - ${hangbang[hb as keyof Object]}`}</Option>
                        ))}
                    </Select>
                </Form.Item>
                <Form.Item label="Trng thi" name="status">
                    <Select>
                        {Object.keys(trangthais).map(hb => (
                            <Option value={hb} key={hb}>{trangthais[hb as keyof Object]}</Option>
                        ))}
                    </Select>
                </Form.Item>
            </Form>
        </Modal>
    )
}

export default ReportFileterModal