import React from 'react'
import {
    Table
} from 'antd'
import moment from 'moment'
import {
    numberWithCommas
} from '../../../../Utils/math'
import { Link } from 'react-router-dom'

const reasons: Record<string,any> = {
    'TT1': 'Thanh ton tin t 1',
    'TT2': 'Thanh ton tin t 2',
    'TLLT': 'Thanh ton tin thi li L thuyt',
    'TLSH': 'Thanh ton tin thi li Sa hnh',
    'TLDT': "Thanh ton tin thi li ng trng",
    'OTHER': 'Thanh ton tin khc',
    'REFUND': "Hon tin - rt h s"
}

type RLType = {
    records: {
        totalRecords: number
        totalAmount: number
        records: Record<string,any>[]
    }
    filter: Record<string,any>
    setFilter: (filter: Record<string,any>) => void
}

const RecordsList:React.FC<RLType> = ({
    records,
    filter,
    setFilter
}) => {
    const columns = [
        {
            title: 'M h s',
            dataIndex: ['student','randomId'],
        },
        {
            title: 'Tn hc vin',
            dataIndex: ['student','ten'],
            render: (text: string, record: any) => {
                return (
                    <Link to={`/std/${record.student.randomId}`}>{text}</Link>
                )
            }
        },
        {
            title: 'S in thoi',
            dataIndex: ['student', 'mobile']
        },
        {
            title: 'Ngy',
            dataIndex: 'createdAt',
            render: (text: string) => <span>{moment(parseFloat(text)).format('DD/MM/YYYY')}</span>
        },
        {
            title: 'Tin',
            dataIndex: 'amount',
            render: (text: string) => <strong style={{ color: parseFloat(text) < 0 ? 'red': 'inherit'}}>{numberWithCommas(text)}</strong>
        },
        {
            title: 'L do',
            dataIndex: 'reason',
            render: (text: string) => <span>{reasons[text]}</span>
        }
    ]
    return (
        <div>
            <Table
                columns={columns}
                dataSource={records.records}
                pagination={{
                    pageSize:100,
                    total: records.totalRecords,
                    onChange: (page: number) => {
                        setFilter({
                            ...filter,
                            page
                        })
                    }
                }}
            />
        </div>
    )
}

export default RecordsList