import React from 'react'
import {
    Modal,
    Form,
    Input
} from 'antd'

type MType = {
    visible: boolean
    onCancel: () => void
    onOk: (password: string, confirmPassword: string) => void
}

const ResetPwdModal:React.FC<MType> = ({
    visible,
    onCancel,
    onOk
}) => {
    const [form] = Form.useForm()
    const [loading, toggleLoading] = React.useState(false)
    const setPwd = async () => {
        toggleLoading(true)
        const {password, confirmPassword}  = form.getFieldsValue()
        await onOk(password, confirmPassword)
        toggleLoading(false)
    }
    return (
        <Modal
            visible={visible}
            onCancel={onCancel}
            title="t li mt khu ngi dng"
            okButtonProps={{ loading}}
            onOk={() => setPwd()}
        >
            <Form form={form} labelCol={{ span: 8}} wrapperCol={{ span: 16}}>
                <Form.Item label="Mt khu mi" name="password" rules={[{required: true, message: 'Bt buc phi c'}, {min: 6, message: 'Ti thiu 6 k t'}]}>
                    <Input.Password />
                </Form.Item>
                <Form.Item label="Nhp lai Mt khu mi" name="confirmPassword" rules={[{required: true, message: 'Bt buc phi c'}, {min: 6, message: 'Ti thiu 6 k t'}]}>
                    <Input.Password />
                </Form.Item>
            </Form>
        </Modal>
    )
}

export default ResetPwdModal