import React from 'react'
import {
    Button,
    PageHeader,
    Tooltip,
    Menu,
    Dropdown,
    message,
    Modal
} from 'antd'
import {
    gql,
    useMutation,
    useLazyQuery,
} from '@apollo/client'
import {
    CaretLeftFilled,
    CheckCircleOutlined,
    CheckOutlined,
    CheckSquareOutlined,
    DeleteOutlined,
    DollarOutlined,
    DownloadOutlined,
    EllipsisOutlined,
    ExclamationOutlined,
    FilterOutlined,
    OrderedListOutlined,
    PlusOutlined,
    PoundOutlined,
    ReloadOutlined,
    RetweetOutlined,
    RollbackOutlined,
    TransactionOutlined,
    UnorderedListOutlined
} from '@ant-design/icons'
import {
    useAuth
} from '../../../context/AuthenticationContext'
import StudentDrawer from './LandingPage/CreateStudentDrawer'
import StudentList from './LandingPage/StudentList'
import FilterModal from './LandingPage/CreateStudentDrawer/FilterModal'
import ReportModal from './Modals/ReportOneModal'
import XLSX from 'xlsx'
import {
    REQUEST_CONFIRM_PAYMENT,
    CONFIRM_PAYMENT,
    REQUEST_WITHDRAW,
    CONFIRM_WITHDRAW,
    LOAD_A_REPORT,
    MOVE_TO_R1,
    REQUEST_CONFIRM_P_2,
    CONFIRM_PAYMENT_TWO,
    MARK_COMPLETED,
    REQUEST_RETURN_RESULT,
    REJECT_WD_FN,
    CONFIRM_WD_GD,
    REJECT_WD_GD,
    MARK_GRAD,
    MARK_FAIL,
    MARK_FOR_RETEST,
    LOAD_B_REPORTS,
    MOVE_TO_R2,
    RESERVE_RESULT,
    ADMIN_CONFIRM_RETURN,
    ADMIN_MARK_FINISH,
    ADMIN_MARK_REMOVE,
    ADMIN_MARK_FINAL_FAIL,
    TEACHER_REQUEST_CONFIRM_RETEST_FEE,
    ADM_CONFIRM_RETEST_FEE
} from '../../common/ClientQueries'
import moment from 'moment'
import StudentStatus from '../../common/StudentStatus'
import CPModal from './Modals/ConfirmPaymentModal'
import RefundModal from './Modals/RefundList'

const CREATE_STUDENT = gql`
    mutation CreateStudentMutation($student: StudentInput!) {
        createStudent(student: $student) {
            randomId
            created
            ten
            ngaysinh
            anh34
            hangbang
            quequan
            cmt1
            cmt2
            bangcap
            dondk
            giaykhamSK
            hopdongdaotao
            trangthai
            mobile
            baoluu {
                thoihan
                trangthai
            }
            kq1 {
                ngay
                lythuyet
                thuchanh
                duongtruong
            }
            kq2 {
                ngay
                lythuyet
                thuchanh
                duongtruong
            }
            createdBy
        }
    }
`
const LOAD_STUDENTS = gql`
    query LoadStudents($filter: FilterInput, $page: Int, $limit: Int, $sort: StdSortInput) {
        loadStudents(filter: $filter, page: $page, limit: $limit, sort: $sort) {
            total
            students {
                _id
                randomId
                created
                ten
                ngaysinh
                anh34
                hangbang
                quequan
                cmt1
                cmt2
                bangcap
                dondk
                giaykhamSK
                hopdongdaotao
                trangthai
                mobile
                # ycdc
                refund
                baocao1 {
                    name
                    _id
                }
                baocao2 {
                    name
                    _id
                }
                baoluu {
                    thoihan
                    trangthai
                }
                kq1 {
                    ngay
                    lythuyet
                    thuchanh
                    duongtruong
                }
                kq2 {
                    ngay
                    lythuyet
                    thuchanh
                    duongtruong
                }
                createdBy
            }
        }
    }
`

const TLandingPage = () => {
    const { getUser } = useAuth()
    const user = getUser()
    // console.log(user)
    const pageSize=100
    const [createDrw, toggleCreateDrw] = React.useState(false)
    const [createStudent] = useMutation(CREATE_STUDENT, { refetchQueries: [LOAD_STUDENTS]})
    const [loadStudents, { loading: loadingStudents, data: students = { loadStudents: { students: [], total: 0} } }] = useLazyQuery(LOAD_STUDENTS, { fetchPolicy: 'network-only' })
    const [exportXLSX, { loading: exporting }] = useLazyQuery(LOAD_STUDENTS, { fetchPolicy: 'network-only', onCompleted: (data: any) => {
        const {loadStudents: { students}} = data
        exportFile(students)
    } })
    const [filterModal, toggleFilterModal] = React.useState(false)
    const [reportModal, toggleReportModal] = React.useState(false)
    const [bc2Modal, toggleBC2Modal] = React.useState(false)
    const [moveR1] = useMutation(MOVE_TO_R1, { refetchQueries: [LOAD_STUDENTS] })
    const [moveR2] = useMutation(MOVE_TO_R2, { refetchQueries: [LOAD_STUDENTS] })
    const [requestConfirmP] = useMutation(REQUEST_CONFIRM_PAYMENT, { refetchQueries: [LOAD_STUDENTS]})
    const [confirmP] = useMutation(CONFIRM_PAYMENT, { refetchQueries: [LOAD_STUDENTS]})
    const [requestWD] = useMutation(REQUEST_WITHDRAW, { refetchQueries: [LOAD_STUDENTS]})
    // const [requestRSV] = useMutation(REQUEST_RESERVE, { refetchQueries: [LOAD_STUDENTS]})
    const [cfWithdraw] = useMutation(CONFIRM_WITHDRAW, { refetchQueries: [LOAD_STUDENTS]})
    // const [cfReserv] = useMutation(CONFIRM_RESERVE_BC1, { refetchQueries: [LOAD_STUDENTS]})
    // const [requestReturn] = useMutation(REQUEST_TO_RETURN, { refetchQueries: [LOAD_STUDENTS]})
    const [requestConfirmP2] = useMutation(REQUEST_CONFIRM_P_2, { refetchQueries: [LOAD_STUDENTS]})
    const [confirmP2] = useMutation(CONFIRM_PAYMENT_TWO, { refetchQueries: [LOAD_STUDENTS]})
    const [markComplete] = useMutation(MARK_COMPLETED, { refetchQueries: [LOAD_STUDENTS] })
    // const [requestReserveResult] = useMutation(REQUEST_RESERVE_RESULT, { refetchQueries: [LOAD_STUDENTS] })
    // const [confirmReserveResult] = useMutation(CONFIRM_RESERVE_RESULT, { refetchQueries: [LOAD_STUDENTS] })
    const [requestReturnResult] = useMutation(REQUEST_RETURN_RESULT, { refetchQueries: [LOAD_STUDENTS] })
    const [rejectWDFN] = useMutation(REJECT_WD_FN, { refetchQueries: [LOAD_STUDENTS] })
    const [acceptWDGD] = useMutation(CONFIRM_WD_GD, { refetchQueries: [LOAD_STUDENTS] })
    const [rejectWDGD] = useMutation(REJECT_WD_GD, { refetchQueries: [LOAD_STUDENTS] })
    const [markGrad] = useMutation(MARK_GRAD, { refetchQueries: [LOAD_STUDENTS] })
    const [markFail] = useMutation(MARK_FAIL, { refetchQueries: [LOAD_STUDENTS] })
    const [markforretest] = useMutation(MARK_FOR_RETEST, { refetchQueries: [LOAD_STUDENTS] })
    const [reserveResult] = useMutation(RESERVE_RESULT, { refetchQueries: [LOAD_STUDENTS] })
    const [admConfirmReturn] = useMutation(ADMIN_CONFIRM_RETURN, { refetchQueries: [LOAD_STUDENTS] })
    const [admMarkFinish] = useMutation(ADMIN_MARK_FINISH, { refetchQueries: [LOAD_STUDENTS] })
    const [admMarkRemove] = useMutation(ADMIN_MARK_REMOVE, { refetchQueries: [LOAD_STUDENTS] })
    const [admMarkFinalFail] = useMutation(ADMIN_MARK_FINAL_FAIL, { refetchQueries: [LOAD_STUDENTS] })
    const [tRequestCRF] = useMutation(TEACHER_REQUEST_CONFIRM_RETEST_FEE, { refetchQueries: [LOAD_STUDENTS] })
    const [admCFRF] = useMutation(ADM_CONFIRM_RETEST_FEE, { refetchQueries: [LOAD_STUDENTS] })
    const [refundModal, toggleRefundModal] = React.useState(false)

    const [filter, setFilter] = React.useState<Record<string,any>>({
        ten: null,
        mobile: null,
        hangbang: null,
        randomId: null,
        trangthai: null
    })
    const [sort, setSort] = React.useState<Record<string,any>>({
        created: -1
    })
    const [currentPage, setCurrentPage] = React.useState(1)
    const [selected, setSelected] = React.useState<string[]>([])
    const [working, toggleWorking] = React.useState(false)
    const [withdrawModal, toggleWDRModal] = React.useState(false)
    const [cpModal, toggleCPModal] = React.useState(false)
    const [cpModalTitle, setCPModalTitle] = React.useState('Xc nhn thanh ton')
    const [confirmCmd, setConfirmCmd] = React.useState<string|null>(null)

    React.useEffect(() => {
        const load = async () => {
            await loadStudents({
                variables: {
                    filter,
                    page: currentPage,
                    limit: pageSize,
                    sort
                }
            })
        }
        load()
    }, [loadStudents, filter, currentPage, sort])
    
    const createStd = async (student: any) => {
        await createStudent({
            variables: {
                student
            }
        })
            // .then(() => loadStudents())
    }
    
    const notFilterEmpty = () => {
        const matchingKey = Object.keys(filter).find(k => filter[k] !== null)
        return Boolean(matchingKey)
    }
    // console.log(notFilterEmpty())
    const onFilterClick = () => {
        if (notFilterEmpty()) {
            // console.log('filter is not empty')
            setFilter({
                ten: null,
                mobile: null,
                hangbang: null,
                randomId: null,
                trangthai: null
            })
            console.log(filter)
        } else {
            toggleFilterModal(!filterModal)
        }
    }

    const rowSelection = {
        onChange: (keys: any) => setSelected(keys),
        selectedRowKeys: selected
    }

    const onMove = async (report: string, type: string) => {
        if (type === 'BC1') {
            await moveR1({
                variables: {
                    report,
                    students: selected
                }
            })
        } else {
            await moveR2({
                variables: {
                    report,
                    students: selected
                }
            })
        }
        setSelected([])
    }

    const doActionWithConfirm = async (command: any, modalTitle: string, msg:string) => {
        if (!selected || selected.length === 0) {
            message.error('Chn h s mun bo lu trc khi thc hin thao tc')
            return
        }
        try {
            Modal.confirm({
                visible: withdrawModal,
                title: modalTitle,
                content: msg,
                onCancel: () => toggleWDRModal(false),
                onOk: async () => {
                    toggleWorking(true)
                    await command({
                        variables: {
                            students: selected
                        }
                    })
                    setSelected([])
                    toggleWorking(false)
                },
                okButtonProps: {
                    loading: working
                }
            })
        } catch (e) {
            message.error(' c li xy ra trong qu trnh thc hin - vui lng kim tra li')
        }
    }

    const doAction = async (command: any ) => {
        if (!selected || selected.length === 0) {
            message.error('Chn h s trc khi thc hin thao tc', 100)
            return
        }
        try {
            toggleWorking(true)
            await command({
                variables: {
                    students: selected
                }
            })
            setSelected([])
            toggleWorking(false)
        } catch (e) {
            message.error(' c li xy ra trong qu trnh thc thi')
        }
    }

    const confirmFinalFail = async (reason: string) => {
        let content = 'nh du cc h s  chn thi trt'
        switch (reason) {
            case 'THI_TRUOT_LT':
                content += ` l thuyt?`
                break;
            case 'THI_TRUOT_SH':
                content +=` Sa hnh`
                break;
            case 'THI_TRUOT_DT':
                content += ` ng trng`
                break;
            default:
                break;
        }
        try {
            Modal.confirm({
                visible: withdrawModal,
                onCancel: () => toggleWDRModal(false),
                onOk: async () => {
                    await admMarkFinalFail({
                        variables: {
                            students: selected,
                            reason
                        }
                    })
                    setSelected([])
                },
                title: 'nh du h s thi trt',
                content
            })
        } catch (e) {
            message.error('Khng th thay i thng tin h s')
        }
    }

    const exportFile = (data: any) => {
        // console.log('exporting....')
        const ws = XLSX.utils.json_to_sheet(data.map((std:Record<string,any>) => ({
            'M H s': std.randomId,
            'Tn': std.ten,
            'Ngy Sinh': moment(parseFloat(std.ngaysinh)).format('DD/MM/YYYY'),
            'Hng bng li': std.hangbang,
            'S in thoi': std.mobile,
            'Trng thi h s': StudentStatus[std.trangthai],
            'Ngy to h s': moment(parseFloat(std.created)).format('DD/MM/YYYY'),
        })))
        const wb = XLSX.utils.book_new()
        XLSX.utils.book_append_sheet(wb, ws, 'Danh sach')
        XLSX.writeFile(wb, 'danhsach.xlsx')
    }

    const onExportClick = async () => {
        await exportXLSX({
            variables: {
                filter,
                page: 1,
                limit: 10000
            }
        })
    }
    let MenuOption = (<Menu />)
    if (user.role === 'TEACHER') {
        MenuOption = (
            <Menu theme="dark">
                <Menu.Item icon={<PlusOutlined />} key="t1" onClick={() => toggleCreateDrw(!createDrw)}>
                    Thm h s
                </Menu.Item>
                <Menu.SubMenu key="_bc" icon={<CaretLeftFilled />} title="Yu cu xc nhn thanh ton">
                    <Menu.Item icon={<DollarOutlined />} key="t2" onClick={() => doAction(requestConfirmP)}>
                        Yu cu XNTT t 1
                    </Menu.Item>
                    <Menu.Item icon={<PoundOutlined />} key="t3" onClick={() => doAction(requestConfirmP2)}>
                        Yu cu XNTT t 2
                    </Menu.Item>
                    <Menu.Item icon={<TransactionOutlined />} key="t4" onClick={() => doAction(tRequestCRF)}>
                        Yu cu XNTT thi li
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.Item icon={<RollbackOutlined />} key="t5" onClick={() => doAction(requestWD)}>
                    Yu cu rt h s
                </Menu.Item>
                <Menu.Item icon={<ReloadOutlined />} key="t6" onClick={() => doAction(requestReturnResult)}>
                    Yu cu quay li thi
                </Menu.Item>
            </Menu>
        )
    }
    if (user.role === 'TEACHER') {
        MenuOption = (
            <Menu theme="dark">
                <Menu.Item icon={<PlusOutlined />} key="t1" onClick={() => toggleCreateDrw(!createDrw)}>
                    Thm h s
                </Menu.Item>
                <Menu.SubMenu key="_bc" icon={<CaretLeftFilled />} title="Yu cu xc nhn thanh ton">
                    <Menu.Item icon={<DollarOutlined />} key="t2" onClick={() => doAction(requestConfirmP)}>
                        Yu cu XNTT t 1
                    </Menu.Item>
                    <Menu.Item icon={<PoundOutlined />} key="t3" onClick={() => doAction(requestConfirmP2)}>
                        Yu cu XNTT t 2
                    </Menu.Item>
                    <Menu.Item icon={<TransactionOutlined />} key="t4" onClick={() => doAction(tRequestCRF)}>
                        Yu cu XNTT thi li
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.Item icon={<RollbackOutlined />} key="t5" onClick={() => doAction(requestWD)}>
                    Yu cu rt h s
                </Menu.Item>
                <Menu.Item icon={<ReloadOutlined />} key="t6" onClick={() => doAction(requestReturnResult)}>
                    Yu cu quay li thi
                </Menu.Item>
            </Menu>
        )
    }
    if (user.role === 'ADMIN') {
        MenuOption = (
            <Menu theme="dark">
                <Menu.SubMenu key="A1" icon={<CaretLeftFilled />} title="Bo co ">
                    <Menu.Item icon={<OrderedListOutlined />} key="_a2" onClick={() => toggleReportModal(true)}>
                        Chuyn bo co 1
                    </Menu.Item>
                    <Menu.Item icon={<UnorderedListOutlined />} key="_a3" onClick={() => toggleBC2Modal(true)}>
                        Chuyn bo co 2
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.SubMenu key="_course" icon={<CaretLeftFilled />} title="Kha hc">
                    <Menu.Item icon={<CheckOutlined />} key="_a4" onClick={() => doAction(markComplete)}>
                        nh du hon thnh kha hc
                    </Menu.Item>
                    <Menu.Item icon={<DeleteOutlined />} key="_a5" onClick={() => doActionWithConfirm(admMarkRemove, 'Hy H s | b hc', 'Bn c chc mun hy cc h s  chn?')}>
                        Hy h s / b hc
                    </Menu.Item>
                    <Menu.Item icon={<CheckSquareOutlined />} key="_a6" onClick={() => doAction(markGrad)}>
                        nh du  tt nghip
                    </Menu.Item>
                    <Menu.Item icon={<ExclamationOutlined />} key="_a7" onClick={() => doAction(markFail)}>
                        nh du trt TN
                    </Menu.Item>
                    <Menu.Item icon={<RetweetOutlined />} key="_a8" onClick={() => doAction(markforretest)}>
                        Vo danh sch thi li TN
                    </Menu.Item>

                    <Menu.Item icon={<UnorderedListOutlined />} key="_a9" onClick={() => doActionWithConfirm(reserveResult, 'Bo lu kt qu', 'Bn c chc mun bo lu kt qu cho cc h s  chn?')}>
                        Bo lu kt qu
                    </Menu.Item>
                    <Menu.Item icon={<ReloadOutlined />} key="_a10" onClick={() => doActionWithConfirm(admConfirmReturn, 'Xc nhn quay li', 'Xc nhn cho cc h s ang bo lu quay li thi')}>
                        Xc nhn quay li thi
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.SubMenu key="_failed" icon={<CaretLeftFilled />} title="Thi trt">
                    <Menu.Item key="a11" onClick={() => confirmFinalFail('THI_TRUOT_LT')}>Thi trt l thuyt</Menu.Item>
                    <Menu.Item key="a12" onClick={() => confirmFinalFail('THI_TRUOT_SH')}>Thi trt Sa hnh</Menu.Item>
                    <Menu.Item key="a13" onClick={() => confirmFinalFail('THI_TRUOT_DT')}>Thi trt ng trng</Menu.Item>
                </Menu.SubMenu>
                <Menu.Item icon={<CheckCircleOutlined color="green" />} key="_a14" onClick={() => doActionWithConfirm(admMarkFinish, 'Xc nhn Hon thnh', 'Xc nhn cho cc h s  chn hon thnh kha hc')}>
                    Xc nhn Hon thnh
                </Menu.Item>
            </Menu>
        )
    }
    if (user.role === 'FINANCE') {
        MenuOption = (
            <Menu theme="dark">
                <Menu.SubMenu key="f1" icon={<CaretLeftFilled />} title="Xc nhn thanh ton">
                    <Menu.Item icon={<DollarOutlined />} key="_f2" onClick={() => {
                        setCPModalTitle('Xc nhn thanh ton t mt')
                        setConfirmCmd('paymentOne')
                        toggleCPModal(true)
                    }}>
                        Xc nhn thanh ton t 1
                    </Menu.Item>
                    {/* <Menu.Item icon={<DollarOutlined />} key="_f2" onClick={() => doAction(confirmP)}>
                        Xc nhn thanh ton t 1
                    </Menu.Item> */}
                    <Menu.Item icon={<PoundOutlined />} key="_f3" onClick={() => {
                        setCPModalTitle('Xc nhn thanh ton t hai')
                        setConfirmCmd('paymentTwo')
                        toggleCPModal(true)
                    }}>
                        Xc nhn thanh ton t 2
                    </Menu.Item>
                    <Menu.Item icon={<CheckCircleOutlined />} key="_f4" onClick={() => {
                        setCPModalTitle('Xc nhn thanh ton thi li')
                        setConfirmCmd('admCFRF')
                        toggleCPModal(true)
                    }}>
                        Xc nhn thanh ton thi li
                    </Menu.Item>
                </Menu.SubMenu>
                <Menu.Item icon={<CheckCircleOutlined />} key="_f5" onClick={() =>{
                    setCPModalTitle('Xc nhn hon tin rt h s')
                    setConfirmCmd('cfWithdraw')
                    toggleCPModal(true)
                }}>
                    Chp thun rt HS
                </Menu.Item>
                {/* <Menu.Item icon={<CheckCircleOutlined />} key="_f5" onClick={() => doAction(cfWithdraw)}>
                    Chp thun rt HS
                </Menu.Item> */}
                <Menu.Item icon={<DeleteOutlined />} key="_f6" onClick={() => doAction(rejectWDFN)}>
                    Khng chp thun rt HS
                </Menu.Item>
            </Menu>
        )
    }
    if (user.role === 'MANAGER') {
        MenuOption = (
            <Menu theme="dark">
                <Menu.Item icon={<CheckCircleOutlined />} key="_m1" onClick={() =>toggleRefundModal(true)}>
                    Chp thun rt HS
                </Menu.Item>
                {/* <Menu.Item icon={<CheckCircleOutlined />} key="_m1" onClick={() => doAction(acceptWDGD)}>
                    Chp thun rt HS
                </Menu.Item> */}
                <Menu.Item icon={<DeleteOutlined />} key="_m2" onClick={() => doAction(rejectWDGD)}>
                    Khng chp thun rt HS
                </Menu.Item>
            </Menu>
        )
    }


    const extras = [
        <Tooltip key="_exprt" title="Ti xung file Excel">
            <Button loading={exporting} shape="round" type="text" icon={<DownloadOutlined />} onClick={() => onExportClick()} />
        </Tooltip>,
        <Tooltip key="_filt" title={notFilterEmpty() ? 'B b lc' : 'Lc h s'}>
            <Button
                danger={notFilterEmpty() || false}
                onClick={() => onFilterClick()}
                key="_Filter"
                shape="round"
                type="text"
                >
                <FilterOutlined />
                {/* {notFilterEmpty() ? 'B b lc' : 'Lc h s'} */}
            </Button>
        </Tooltip>,
        <Tooltip title="Ti li" key="_reload">
            <Button type="text" key="_reload" onClick={() => loadStudents({ variables: { filter, page: currentPage, limit: pageSize } })} shape="round">
                <ReloadOutlined />
            </Button>
        </Tooltip>,
        <Dropdown trigger={['click']} key="_options" overlay={MenuOption}>
            <Tooltip title="Chc nng">
                <Button type="primary" icon={<EllipsisOutlined />} loading={working} />
            </Tooltip>
        </Dropdown>
    ]
    
    return (
        <div>
            <PageHeader
                title={`Danh sch h s`}
                extra={extras}
            />
            <StudentList
                rowSelection={rowSelection}
                students={students.loadStudents}
                loading={loadingStudents}
                currentPage={currentPage}
                setCurrentPage={setCurrentPage}
                pageSize={pageSize}
                sort={sort}
                setSort={setSort}
            />
            <div>
                <StudentDrawer
                    visible={createDrw}
                    onClose={() => toggleCreateDrw(false)}
                    createStudent={createStd}
                />
            </div>
            <FilterModal
                filter={filter}
                setFilter={setFilter}
                visible={filterModal}
                onCancel={() => toggleFilterModal(false)}
                onOk={() => toggleFilterModal(false)}
            />
            <ReportModal
                dataKey="loadAReport"
                query={LOAD_A_REPORT}
                onMove={(report: string) => onMove(report, 'BC1')}
                visible={reportModal}
                onCancel={() => toggleReportModal(false)}
                onOk={() => toggleReportModal(false)}
                />
            <ReportModal
                dataKey="loadBreports"
                query={LOAD_B_REPORTS}
                onMove={(report: string) => onMove(report, 'BC2')}
                visible={bc2Modal}
                onCancel={() => toggleBC2Modal(false)}
                onOk={() => toggleBC2Modal(false)}
            />
            <CPModal
                title={cpModalTitle}
                visible={cpModal}
                onCancel={() => toggleCPModal(false)}
                onOk={()=>null}
                confirm={{
                    'paymentOne': confirmP,
                    'paymentTwo': confirmP2,
                    'admCFRF': admCFRF,
                    'cfWithdraw': cfWithdraw
                }}
                command={confirmCmd}
                selected={selected}
            />
            <RefundModal
                students={students.loadStudents.students.filter((s:any) => {
                    return selected.indexOf(s._id) >= 0
                })}
                // students={[]}
                onOk={() => doAction(acceptWDGD)}
                visible={refundModal}
                onCancel={()=>toggleRefundModal(false)}
            />
        </div>
    )
}

export default TLandingPage