import React from 'react'
import {
    Modal,
    Form,
    Input,
    Select,
    message
} from 'antd'
import grades from '../../common/HangBang'

const { Option } = Select

type CType = {
    visible: boolean
    onCancel: () => void
    onOk: (r: Record<string,any>) => void
    type: string
}

const CreateReportModal:React.FC<CType> = ({
    visible,
    onCancel,
    onOk,
    type
}) => {
    const [form] = Form.useForm()
    const [creating, toggleCreating] = React.useState(false)

    const onCreate = async () => {
        toggleCreating(true)
        const report = form.getFieldsValue()
        console.log(report)
        if (!report.name || !report.hangbang) {
            message.error('Khng th to bo co do thiu thng tin')
            onCancel()
        }
        toggleCreating(false)
        onOk(report)
    }
    return (
        <Modal
            visible={visible}
            onCancel={onCancel}
            onOk={onCreate}
            title={`To mi bo co mi - ${type === 'BC1' ? 'Bo co 1' : 'Bo co 2'}`}
            okButtonProps={{
                loading: creating
            }}
        >
            <Form form={form} labelCol={{ span: 6 }} wrapperCol={{ span: 18 }}>
                <Form.Item label="Tn bo co" name="name" rules={[{ required: true, message: 'Bt buc phi c tn'}]}>
                    <Input placeholder="bo co mt - ngy/thng/nm" />
                </Form.Item>
                <Form.Item label="Hng bng" name="hangbang" rules={[{ required: true, message: 'Bt buc phi c hng bng li'}]}>
                    <Select>
                        {Object.keys(grades).map(g => (
                            <Option key={g} value={g}>{`${g} -  ${grades[g as keyof Object]}`}</Option>
                        ))}
                    </Select>
                </Form.Item>
                <Form.Item label="Ghi ch" name="desc">
                    <Input placeholder="Ghi ch & din gii" />
                </Form.Item>
            </Form>
        </Modal>
    )
}
export default CreateReportModal