import React from 'react'
import {
    Form,
    Input,
    DatePicker,
    Upload,
    ConfigProvider,
    Button,
    Space,
    message,
    Select
} from 'antd'
import ImgCrop from 'antd-img-crop'
import {
    gql,
    useMutation
} from '@apollo/client'
import viVN from 'antd/lib/locale/vi_VN'
import moment from 'moment'
import hangbangs from '../../../../common/HangBang'

const {Option} = Select

const UPLOAD_AVATAR = gql`
    mutation uploadAvatar($file: Upload!) {
        uploadAvatar(file: $file) {
            url
        }
    }
`

const fLayout = {
    wrapperCol: { span: 18 },
    labelCol: { span: 6 }
}
interface CTypes {
    createStudent: (data: any) => void
    close: () => void
}
const CreateStudentForm: React.FC<CTypes> = ({
    createStudent,
    close
}) => {
    const [form] = Form.useForm()
    const [imgList, setImgList] = React.useState([])
    const [cmt1, setCmt1] = React.useState([])
    const [cmt2, setCmt2] = React.useState([])
    const [uploadFile] = useMutation(UPLOAD_AVATAR)
    const [creating, setCreating] = React.useState(false)
    const onAvatarChange = (key: string, { file, fileList: newImgsList }: any) =>  {
        if (key === 'anh34') {
            setImgList(newImgsList)
        }
        if (key === 'cmt1') {
            setCmt1(newImgsList)
        }
        if (key === 'cmt2') {
            setCmt2(newImgsList)
        }
    }
    const onPreview = async (file:any) => {
        let src = file.url;
        if (!src) {
            src = await new Promise(resolve => {
                const reader = new FileReader();
                reader.readAsDataURL(file.originFileObj);
                reader.onload = () => resolve(reader.result);
            });
        }
        const image = new Image();
        image.src = src;
        const imgWindow:any = window.open(src);
        imgWindow.document.write(image.outerHTML);
    };
    const customUpload = async (field:string, { file, onSuccess }: any) => {
        await uploadFile({
            variables: {
                file
            }
        })
        .then(res => {
            if (!res || !res.data) {
                message.error('Khng ti ln c hnh nh')
            }
            // console.log(res)
            const oldData = form.getFieldsValue()
            oldData[field] = res.data.uploadAvatar.url
            form.setFieldsValue(oldData)
            console.log(oldData)
        })
        onSuccess('ok')
    }
    const create = async () => {
        setCreating(true)
        try {
            const data = form.getFieldsValue()
            console.log(data)
            await createStudent({
                ...data,
                ngaysinh: moment(data.ngaysinh).toDate()
            })
        } catch (e) {
            message.error(' c li trong qu trnh x l, vui lng th li sau')
        }
        setCreating(false)
        form.setFieldsValue({})
        close()
    }
    return (
        <ConfigProvider locale={viVN}>
            <Form form={form} {...fLayout}>
                <Form.Item rules={[
                    {
                        required: true,
                        message: "Bt buc phi c"
                    }
                ]} name="ten" label="H v tn hc vin">
                    <Input />
                </Form.Item>
                <Form.Item name="ngaysinh" label="Ngy thng nm sinh" rules={[{required: true, message: "Bt buc phi c"}]}>
                    <DatePicker
                        format="DD/MM/YYYY"
                        placeholder="Chn ngy thng nm sinh"
                    />
                </Form.Item>
                <Form.Item name="quequan" label="Qu Qun" rules={[{ required: true, message: 'Trng bt buc' }]}>
                    <Input />
                </Form.Item>
                <Form.Item label="Upload nh">
                    <Space align="center" size="large">
                        <div style={{ display: "block", textAlign: 'center'}}>
                            <p>nh th 3x4</p>
                            <ImgCrop
                                modalTitle="cn chnh nh"
                                aspect={3 / 4}
                            >
                                <Upload
                                    customRequest={(data) => customUpload('anh34', data)}
                                    maxCount={1}
                                    listType="picture-card"
                                    fileList={imgList}
                                    onChange={(data) => onAvatarChange('anh34', data)}
                                    onPreview={onPreview}
                                    >
                                    {imgList.length < 1 && '+ Upload'}
                                </Upload>
                            </ImgCrop>
                        </div>
                        <div style={{ display: "block", textAlign: 'center'}}>
                            <p>nh mt trc CMT</p>
                            <ImgCrop
                                modalTitle="cn chnh nh"
                                aspect={5 / 3}
                                >
                                <Upload
                                    customRequest={(data) => customUpload('cmt1', data)}
                                    maxCount={2}
                                    listType="picture-card"
                                    fileList={cmt1}
                                    onChange={(data) => onAvatarChange('cmt1', data)}
                                    onPreview={onPreview}
                                    >
                                    {cmt1.length < 1 && '+ Upload'}
                                </Upload>
                            </ImgCrop>
                        </div>
                        <div style={{ display: "block", textAlign: 'center'}}>
                            <p>nh mt sau CMT</p>
                            <ImgCrop
                                modalTitle="cn chnh nh"
                                aspect={5 / 3}
                            >
                                <Upload
                                    customRequest={(data) => customUpload('cmt2', data)}
                                    maxCount={1}
                                    listType="picture-card"
                                    fileList={cmt2}
                                    onChange={(data) => onAvatarChange('cmt2', data)}
                                    onPreview={onPreview}
                                >
                                    {cmt2.length < 1 && '+ Upload'}
                                </Upload>
                            </ImgCrop>
                        </div>
                    </Space>
                </Form.Item>
                <Form.Item hidden name="anh34" label="nh 3 x 4" rules={[{ required: true, message: 'Phi c mt nh 3x4'}]} />
                <Form.Item hidden name="cmt1" label="nh CMT/CCCD mt trc" rules={[{ required: true, message: 'Phi c mt nh' }]} />
                <Form.Item hidden name="cmt2" label="nh CMT/CCCD mt sau" rules={[{ required: true, message: 'Phi c mt nh' }]} />
                <Form.Item label="Hng bng li" name="hangbang">
                    <Select>
                        {Object.keys(hangbangs).map(k => (
                            <Option value={k} key={k}>{`${k} - ${hangbangs[k as keyof Object]}`}</Option>
                        ))}
                    </Select>
                </Form.Item>
                <Form.Item name="mobile" label="S in thoi">
                    <Input />
                </Form.Item>
                <Space>
                    <Button shape="round" loading={creating} onClick={() => create()} type="primary">To h s</Button>
                    <Button shape="round" onClick={() => close()}>Hy</Button>
                </Space>
            </Form>
        </ConfigProvider>
    )
}
export default CreateStudentForm