import React from 'react'
import {
    Typography,
    Divider,
    Descriptions,
    // Button,
    Image
} from 'antd'
import moment from 'moment'
import status from '../../../common/StudentStatus'

const { Title } = Typography

type BasicInfoType = {
    student: any,
    role: string
}


const BasicInfo:React.FC<BasicInfoType> = ({
    student,
    role,
}) => {
    return (
        <div>
            <Divider />
            <Descriptions
                layout="vertical"
                title={<Title level={4}>Thng tin c bn</Title>}
            >
                <Descriptions.Item label="H v tn">
                    <strong>
                        {student.ten}
                    </strong>
                </Descriptions.Item>
                <Descriptions.Item label="Ngy thng nm sinh">{moment(parseFloat(student.ngaysinh)).format('DD/MM/YYYY')}</Descriptions.Item>
                <Descriptions.Item label="Qu qun">{student.quequan}</Descriptions.Item>
                <Descriptions.Item label="S in thoi">{student.mobile}</Descriptions.Item>
                <Descriptions.Item label="CMND">
                    <Image src={`http://localhost:5001/${student.anh34}`} />
                </Descriptions.Item>
            </Descriptions>
            <Divider />
            <Descriptions
                layout="vertical"
                title={<Title level={4}>Thng tin o to</Title>}
            >
                <Descriptions.Item label="Hng bng li">
                    <strong>{student.hangbang}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="S in thoi">
                    <strong>{student.mobile}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="Trng thi">
                {/* @ts-ignore */}
                    <strong>{status[student.trangthai]}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="Bo co 1">
                    <strong>{student.baocao1?.name || 'Khng c thng tin'}</strong>
                </Descriptions.Item>
                <Descriptions.Item label="Bo co 2">
                    <strong>{student.baocao2?.name || 'Khng c thng tin'}</strong>
                </Descriptions.Item>
            </Descriptions>
        </div>
    )
}
export default BasicInfo